.calculatorWrap {
    display: flex;
    flex-direction: row;
    width: 80%;
    justify-content: space-evenly;
}
.calculator {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 2px;
    background-color: #191717;
    padding: 4px;
    border-radius: 5px;
    width: 360px;
    height: 550px;
    font-family: "Montserrat";
}

.item {
    background-color: rgb(255, 133, 51);
    color: #191717;
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 22px;
    cursor: pointer;
    font-weight: bold;
}

.form {
    max-width: 100%;
    min-height: 60px;
    grid-column: 1/-1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.clean {
    grid-column: 1/3;
}

.back {
    grid-column: 3/-1;
}

.equal {
    grid-column: 4;
    grid-row: 6/8;
}

.zero {
    grid-column: 1/3;
}

.result {
    width: 360px;
    height: 400px;
    border: 4px solid #191717;
    border-radius: 5px;
}

.result p {
    text-align-last: left;
    padding: 10px;
    margin: 0;
}